<template>
  <!--  业务管理-->
  <div class="business-management">
    <div class="form-container">
      <el-input
        v-model="paramsData.companyName"
        placeholder="请输企业名称"
        class="company"
        clearable
      />
      <el-input
        v-model="paramsData.companyCode"
        placeholder="请输企业代码"
        class="company"
        clearable
      />
      <el-date-picker
        v-model="initData.dateList"
        type="daterange"
        range-separator="~"
        start-placeholder="到期开始日期"
        end-placeholder="到期结束日期"
        @change="changeDate"
        :clearable="false"
        format="YYYY-MM-DD"
        :disabled-date="
          function (time) {
            return time.getTime() > Date.now();
          }
        "
      />
      <el-button type="primary" class="distance" @click="getList"
        >搜索</el-button
      >
      <!--      <el-button type="primary" class="distance" @click="handleExport"-->
      <!--        >导出</el-button-->
      <!--      >-->
    </div>
    <div class="table-container">
      <!--   列表   -->
      <el-table :data="pageData.list" height="calc(100vh - 260px)">
        <el-table-column
          label="企业名称"
          prop="companyNameStr"
          align="center"
          min-width="200"
        />
        <el-table-column
          label="企业代码"
          prop="companyCode"
          align="center"
          min-width="150"
        />
        <el-table-column
          label="当前线路"
          prop="lineName"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="可用线路"
          prop="lineNames"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="号码数量"
          prop="phoneNum"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="服务周期"
          prop="serviceMonth"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="总使用量"
          prop="allUse"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="月度可使用数量"
          prop="minuteNum"
          align="center"
          min-width="180"
        />
        <el-table-column
          label="月度已使用数量"
          prop="useMinuteNum"
          align="center"
          min-width="180"
        />
        <el-table-column
          label="总月度数"
          prop="monthNum"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="已使用月度数"
          prop="useMonthNum"
          align="center"
          min-width="180"
        />
        <el-table-column
          label="坐席数量"
          prop="seatNumber"
          align="center"
          min-width="120"
        />
        <el-table-column
          label="开始时间"
          prop="startTime"
          align="center"
          min-width="200"
        />
        <el-table-column
          label="到期时间"
          prop="endTime"
          align="center"
          min-width="200"
        />
        <template #empty>
          <TableEmpty empty-text="暂无数据"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { shallowReactive } from "vue";

import { usePage, downloadExcel, LocalStorage } from "jz-tool-lib";

import { callAgencyListApi } from "@api/business-management-api";

defineOptions({
  name: "businessManagement.ospreyCloudCall.resource.list",
});

const { bd_userInfo: userInfo } = LocalStorage.batchGetItem(["bd_userInfo"]);

//代理商公司id
const initData = shallowReactive({
  dateList: [],
});
const paramsData = shallowReactive({
  companyCode: "",
  companyName: "",
  agentUserId: userInfo?.profileId,
  startTime: "",
  endTime: "",
});
const { getList, pageNumberChange, pageSizeChange, pageData, resetPageNumber } =
  usePage({
    listApi: callAgencyListApi,
    props: {
      pageNumber: "pageNo",
      pageSize: "pageSize",
    },
    paramsData,
  });
const changeDate = (value: any[]) => {
  if (value) {
    paramsData.startTime = dayjs(value[0])
      .startOf("day")
      .format("YYYY-MM-DD HH:mm:ss");
    paramsData.endTime = dayjs(value[1])
      .endOf("day")
      .format("YYYY-MM-DD HH:mm:ss");
  } else {
    paramsData.startTime = dayjs().startOf("day").format("YYYY-MM-DD HH:mm:ss");
    paramsData.endTime = dayjs().endOf("day").format("YYYY-MM-DD HH:mm:ss");
  }
  resetPageNumber();
  getList();
};
// table导出表格
const handleExport = async () => {
  const { data } = await callAgencyListApi.getData({
    pageNo: 1,
    pageSize: 2000,
    agentUserId: userInfo?.profileId,
  });
  const tableData = callAgencyListApi.handleData(data).list;
  const columnData = [
    { width: "200px", label: "企业名称", prop: "companyNameStr" },
    { width: "200px", label: "企业代码", prop: "companyCode" },
    { width: "150px", label: "当前线路", prop: "lineName" },
    { width: "120px", label: "可用线路", prop: "lineNames" },
    { width: "120px", label: "号码数量", prop: "phoneNum" },
    { width: "120px", label: "服务周期", prop: "serviceMonth" },
    { width: "120px", label: "总使用量", prop: "allUse" },
    { width: "120px", label: "月度可使用数量", prop: "minuteNum" },
    { width: "180px", label: "月度已使用数量", prop: "useMinuteNum" },
    { width: "180px", label: "总月度数", prop: "monthNum" },
    { width: "200px", label: "已使用月度数", prop: "useMonthNum" },
    { width: "180px", label: "坐席数量", prop: "seatNumber" },
    { width: "120px", label: "开始时间", prop: "startTime" },
    { width: "200px", label: "到期时间", prop: "endTime" },
  ];
  downloadExcel(tableData, columnData);
};
</script>
<style lang="scss" scoped>
.form-container {
  justify-content: flex-end;
  .el-input {
    width: 200px;
    margin-right: 10px;
  }
  :deep(.el-date-editor.el-input__wrapper) {
    width: 300px;
    flex-grow: inherit;
    margin-right: 10px;
  }
}
</style>
